.main-screen {
  margin-top: 0;
  padding: 140px 0 170px;

  background-image: url("../img/main-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.main-screen__title {
  font-weight: 800;
  font-size: 70px;
  text-align: center;
  color: $basic-white;
}

.main-screen__subtitle {
  position: relative;

  margin-bottom: 0;
  padding-bottom: 20px;

  font-weight: 400;
  font-size: 20px;
  text-align: center;
  color: $basic-white;

  background-image: url("../img/line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
}
