.contacts {
  padding: 50px 0 0;

  background-color: $basic-grey;
}

.contacts__title {
  font-weight: 800;
  font-size: 50px;
  text-align: center;
  color: $basic-white;
}

.contacts__subtitle {
  font-size: 20px;
  text-align: center;
  color: $basic-white;
}

.contacts__list {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  margin-top: 50px;

  font-size: 18px;
  color: $basic-white;
}

.contacts__list-item--email {
  padding-left: 25px;

  background-image: url("../img/mail.svg");
  background-repeat: no-repeat;
  background-position: left 5px;
}

.contacts__list-item-link {
  margin-bottom: 0;

  color: $basic-white;
  text-decoration: none;

  &:hover {
    color: $basic-white;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.contacts__list-item--location {
  padding-left: 25px;

  background-image: url("../img/location.svg");
  background-repeat: no-repeat;
  background-position: left 3px;
}

.contacts__list-item--phone {
  padding-left: 25px;

  background-image: url("../img/phone2.svg");
  background-repeat: no-repeat;
  background-position: left 3px;
}

.contacts__map {
  width: 100%;
  margin-top: 50px;
}
