.attendance {
  margin-top: 80px;
}

.attendance__title {
  font-weight: 800;
  font-size: 40px;
  text-align: center;
}

.attendance__subtitle {
  margin-bottom: 50px;
  padding-bottom: 20px;

  font-size: 20px;
  text-align: center;

  background-image: url("../img/line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
}

.attendance__item {
  margin-bottom: 40px;
}

.attendance__item-header {
  margin-top: 20px;

  font-weight: 600;
  font-size: 22px;
  text-align: center;
}

.attendance__item-subheader {
  font-size: 16px;
  text-align: center;
  color: $special-black;
}

.attendance__btn {
  display: block;
  width: 200px;
  margin: 0 auto;
  margin-top: 40px;
  padding: 10px 20px;

  font-weight: 600;
  text-align: center;
  color: $basic-black;
  text-decoration: none;

  background-color: $basic-orange;
  border-radius: 5px;

  &:hover {
    color: $basic-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}
