.clients {
  margin-top: 80px;
}

.private {
  position: relative;

  padding: 180px 0;

  background-color: $basic-orange;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    width: calc((1000vw - 337px) / 2);
    height: 100%;

    background-color: $basic-orange;
  }

  & .clients__title {
    font-size: 50px;
  }
}

.for-clients {
  position: relative;

  padding: 180px 0;

  background-color: $basic-grey;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: calc((1000vw - 337px) / 2);
    height: 100%;

    background-color: $basic-grey;
  }

  & .clients__title,
  .clients__subtitle {
    color: $basic-white;
  }
}

.clients__title {
  font-weight: 700;
  font-size: 32px;
  text-align: center;
}

.clients__subtitle {
  margin-top: 20px;
  margin-bottom: 0;

  font-size: 18px;
  text-align: center;
}

.private-btn {
  display: block;
  width: 150px;
  margin: 0 auto;
  margin-top: 40px;
  padding: 10px 20px;

  font-weight: 600;
  text-align: center;
  color: $basic-black;
  text-decoration: none;

  background-color: $basic-white;
  border-radius: 5px;

  &:hover {
    color: $basic-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}
