.request {
  margin-top: 80px;
  padding: 100px 0;

  background-image: url("../img/main-bg.jpg");
  background-size: cover;
}

.request__title {
  font-weight: 600;
  text-align: center;
  color: $basic-white;
}

.request__btn {
  display: block;
  width: 200px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 80px;
  padding: 10px 20px;

  font-weight: 500;
  text-align: center;
  color: $basic-black;
  text-decoration: none;

  background-color: $basic-orange;
  border-radius: 5px;

  &:hover {
    color: $basic-black;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.request__section {
  padding: 150px 0;

  text-align: center;

  background-color: $basic-orange;
  border-radius: 5px;
}

.request__header {
  font-weight: 700;
  font-size: 30px;
}

.request__subheader {
  font-size: 18px;
}

.request__form {
  padding: 50px 40px;

  background-color: $basic-white;
  border-radius: 5px;
}

.request__form-input {
  width: 100%;
  margin-bottom: 20px;
  padding: 8px 15px;

  border: 1px solid $basic-light-grey;
  border-radius: 5px;
}

.request__form-input--textarea {
  min-height: 100px;

  resize: none;
}

.request__form-btn {
  padding: 10px 20px;

  font-weight: 600;

  background-color: $basic-orange;
  border: none;
  border-radius: 5px;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.7;
  }
}
