.page-header {
  padding: 10px 0 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  display: flex;
  align-items: center;
}

.header__logo-text {
  margin-bottom: 0;
  margin-left: 20px;

  font-weight: 700;
  font-size: 26px;
  line-height: 24px;

  & span {
    font-weight: 400;
    font-size: 16px;
  }
}

.header__socials-link {
  margin-right: 15px;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }

  &:last-child {
    margin-right: 0;
  }
}

.header__phone-link {
  padding-left: 25px;

  font-weight: 600;
  font-size: 20px;
  color: $basic-black;
  text-decoration: none;

  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: left top;

  &:hover {
    color: $basic-black;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.header__phone-text {
  margin-bottom: 0;

  text-align: right;
  color: $special-black;
}

.header__sign-in {
  margin-bottom: 0;
  padding-left: 20px;

  font-weight: 500;
  color: $basic-black;
  text-decoration: none;

  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    color: $basic-black;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.header_shopping-card {
  margin-bottom: 0;
  padding-left: 25px;

  font-weight: 500;
  color: $basic-black;
  text-decoration: none;

  background-image: url("../img/shopping.svg");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    color: $basic-black;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.header__nav {
  margin-top: 10px;
  margin-bottom: 0;
  padding: 10px 0;

  background-color: $basic-orange;
}

.header__nav-list {
  @include reset-list;

  display: flex;
  justify-content: space-around;
}

.header__nav-list-item-link {
  color: $basic-white;
  text-decoration: none;

  &:hover {
    color: $basic-white;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

@media screen and (max-width: 768px) {
  .header {
    align-items: flex-start;
  }
}

@media screen and (max-width: 550px) {
  .header {
    flex-direction: column;
  }

  .header__phone {
    margin-top: 20px;
  }
}
