.advantages {
  margin-top: 80px;
}

.advantages__title {
  padding-bottom: 20px;

  font-weight: 800;
  font-size: 40px;
  text-align: center;

  background-image: url("../img/line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
}

.advantages__list {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.advantages__item-text {
  margin-top: -25px;

  text-align: center;
  color: $special-black;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
