.services {
  margin-top: 80px;
}

.services__title {
  font-weight: 700;
  font-size: 28px;
  text-align: center;
}

.services__subtitle {
  margin-top: 20px;
  margin-bottom: 0;

  font-size: 18px;
  text-align: center;
}

.repair {
  position: relative;

  padding: 180px 0;

  background-color: $basic-grey;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    width: calc((1000vw - 337px) / 2);
    height: 100%;

    background-color: $basic-grey;
  }

  & .services__title {
    font-size: 50px;
    color: $basic-white;
  }

  & .services__subtitle {
    color: $basic-white;
  }
}

.programme {
  position: relative;

  padding: 180px 0;

  background-color: $basic-orange;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: calc((1000vw - 337px) / 2);
    height: 100%;

    background-color: $basic-orange;
  }
}
